<template>
  <div class="container">
    <div class="lucky-wheel">
      <div class="lucky-title"></div>
      <div class="wheel-main">
        <div class="wheel-pointer-box">
          <div class="wheel-pointer" @click="rotate_handle()" :style="{transform:rotate_angle_pointer,transition:rotate_transition_pointer}"></div>
        </div>
        <div class="wheel-bg" :style="{transform:rotate_angle,transition:rotate_transition}">
          <div id="circle0">
            <div id="left">
              <div class="circle-left" id="circle-l1">
                <span>一套护肤品</span>
              </div>
              <div class="circle-left" id="circle-l2">
                <span>发语音道歉</span>
              </div>
              <div class="circle-left" id="circle-l3">
                <span>一顿火锅</span>
              </div>
              <div class="circle-left" id="circle-l4">
                <span>一件衣服</span>
              </div>
              <div class="circle-left" id="circle-l5">
                <span>无条件原谅</span>
              </div>
              <div class="circle-left" id="circle-l6">
                <span>一支口红</span>
              </div>
              <div class="circle-left" id="circle-l7">
                <span>发朋友圈承认错误</span>
              </div>
              <div class="circle-left" id="circle-l8">
                <span>购物车清空3样物品</span>
              </div>
              <div class="circle-left" id="circle-l9">
                <span>跪键盘1小时</span>
              </div>
            </div>
            <div id="right">
              <div class="circle-right" id="circle-r1">
                <span>一瓶香水</span>
              </div>
              <div class="circle-right" id="circle-r2">
                <span>跳脱衣舞</span>
              </div>
              <div class="circle-right" id="circle-r3">
                <span>无条件原谅</span>
              </div>
              <div class="circle-right" id="circle-r4">
                <span>800字检讨</span>
              </div>
              <div class="circle-right" id="circle-r5">
                <span>打十下屁股</span>
              </div>
              <div class="circle-right" id="circle-r6">
                <span>一个蛋糕</span>
              </div>
              <div class="circle-right" id="circle-r7">
                <span>转账100元</span>
              </div>
              <div class="circle-right" id="circle-r8">
                <span>一根烤肠</span>
              </div>
              <div class="circle-right" id="circle-r9">
                <span>立刻打扫卫生</span>
              </div>

            </div>
          </div>
          <!-- <div class="prize-list">
            <div class="prize-item" v-for="(item,index) in prize_list" :key="index">
              <div class="prize-pic">
                <img :src="item.icon">
              </div>
              <div class="prize-count" v-if="item.count">
                {{item.count}}
              </div>
              <div class="prize-type">
                {{item.name}}
              </div>
            </div>
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      easejoy_bean: 0, //金豆
      lottery_ticket: 0, //抽奖次数

      toast_control: false, //抽奖结果弹出框控制器
      hasPrize: false, //是否中奖
      start_rotating_degree: 0, //初始旋转角度
      rotate_angle: 0, //将要旋转的角度
      start_rotating_degree_pointer: 0, //指针初始旋转角度
      rotate_angle_pointer: 0, //指针将要旋转的度数
      rotate_transition: 'transform 6s ease-in-out', //初始化选中的过度属性控制
      rotate_transition_pointer: 'transform 12s ease-in-out', //初始化指针过度属性控制
      click_flag: true, //是否可以旋转抽奖
      i: 0 //测试使用
    }
  },
  methods: {
    //此方法为处理奖品数据
    rotate_handle() {
      this.rotating()
      this.i = this.i + 2
    },
    rotating(index) {
      if (!this.click_flag) return
      var type = 0 // 默认为 0  转盘转动 1 箭头和转盘都转动(暂且遗留)
      var during_time = 5 // 默认为1s
      var random = Math.floor(Math.random() * 7)
      var result_index = index || random // 最终要旋转到哪一块，对应prize_list的下标
      var result_angle = [337.5, 292.5, 247.5, 202.5, 157.5, 112.5, 67.5, 22.5] //最终会旋转到下标的位置所需要的度数
      var rand_circle = 6 // 附加多转几圈，2-3
      this.click_flag = false // 旋转结束前，不允许再次触发
      if (type == 0) {
        // 转动盘子
        var rotate_angle =
          this.start_rotating_degree +
          rand_circle * 360 +
          result_angle[result_index] -
          (this.start_rotating_degree % 360)
        this.start_rotating_degree = rotate_angle
        this.rotate_angle = 'rotate(' + rotate_angle + 'deg)'
        var that = this
        // 旋转结束后，允许再次触发
        setTimeout(function () {
          that.click_flag = true
          that.game_over(this.i)
        }, during_time * 1000 + 1500) // 延时，保证转盘转完
      } else {
        //
      }
    },
    game_over() {
      this.toast_control = true
      // this.hasPrize = this.prize_list[this.i].isPrize
    }
  }
}
</script>
<style>
