  width: 18.4375rem;
  height: 18.4375rem;
  background: url('../assets/img/turntable.png') no-repeat center top;
  background-size: 100%;
  color: #fff;
  font-weight: 500;
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  transition: transform 3s ease;
}
.wheel-pointer-box {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 100;
  transform: translate(-50%, -50%);
  width: 5.3125rem;
  height: 5.3125rem;
}
.wheel-pointer {
  width: 4.3125rem;
  height: 5.3125rem;
  background: url('../assets/img/reward.png') no-repeat center top;
  background-size: 100%;
  margin: 0 auto;
  transform-origin: center 60%;
}
.wheel-bg .circle-left {
  width: 7.05rem;
  height: 14.1rem;
  border-radius: 0px 7.05rem 7.05rem 0px;
  position: absolute;
  right: 0;
  transform-origin: 0 50%;
}
.wheel-bg .circle-right {
  width: 7.05rem;
  height: 14.1rem;
  border-radius: 7.05rem 0px 0px 7.05rem;
  position: absolute;
  right: 0;
  transform-origin: 100% 50%;
}
.wheel-bg .circle-left span {
  text-align: center;
  font-size: 0.4rem;
  margin: 0 auto;
  writing-mode: vertical-lr;
  margin-left: 0.65rem;
  margin-top: 0.6rem;
  transform: rotate(10deg);
  color: #fff;
}
.wheel-bg .circle-right span {
  text-align: center;
  font-size: 0.4rem;
  float: right;
  margin: 0 auto;
  writing-mode: vertical-lr;
  margin-right: 0.65rem;
  margin-top: 0.6rem;
  transform: rotate(-10deg);
  color: #fff;
}
.wheel-bg #circle0 {
  width: 14.1rem;
  height: 14.1rem;
  border-radius: 7.05rem;
  background-color: yellowgreen;
  position: relative;
}
.wheel-bg #circle-l1 {
  background-color: #70f3ff;
  transform: rotate(0deg);
}
.wheel-bg #circle-l2 {
  background-color: #39ffa3;
  transform: rotate(20deg);
}
.wheel-bg #circle-l3 {
  background-color: #7364f2;
  transform: rotate(40deg);
}
.wheel-bg #circle-l4 {
  background-color: #ff70e2;
  transform: rotate(60deg);
}
.wheel-bg #circle-l5 {
  background-color: #f13f70;
  transform: rotate(80deg);
}
.wheel-bg #circle-l6 {
  background-color: #ff8f70;
  transform: rotate(100deg);
}
.wheel-bg #circle-l7 {
  background-color: #00eaff;
  transform: rotate(120deg);
}
.wheel-bg #circle-l8 {
  background-color: #9e42d4;
  transform: rotate(140deg);
}
.wheel-bg #circle-l9 {
  background-color: #70f3ff;
  transform: rotate(160deg);
}
.wheel-bg #circle-r1 {
  background-color: #d57d3a;
  transform: rotate(0deg);
}
.wheel-bg #circle-r2 {
  background-color: #39ffa3;
  transform: rotate(-20deg);
}
.wheel-bg #circle-r3 {
  background-color: #7364f2;
  transform: rotate(-40deg);
}
.wheel-bg #circle-r4 {
  background-color: #ff70e2;
  transform: rotate(-60deg);
}
.wheel-bg #circle-r5 {
  background-color: #f13f70;
  transform: rotate(-80deg);
}
.wheel-bg #circle-r6 {
  background-color: #ff8f70;
  transform: rotate(-100deg);
}
.wheel-bg #circle-r7 {
  background-color: #00eaff;
  transform: rotate(-120deg);
}
.wheel-bg #circle-r8 {
  background-color: #9e42d4;
  transform: rotate(-140deg);
}
.wheel-bg #circle-r9 {
  background-color: #ff7070;
  transform: rotate(-160deg);
}
.wheel-bg #left {
  clip: rect(0px 7.05rem 14.1rem 0px);
  position: absolute;
  right: 0px;
  width: 7.05rem;
  height: 14.1rem;
  overflow: hidden;
}
.wheel-bg #right {
  clip: rect(0px 7.05rem 14.1rem 0px);
  position: absolute;
  left: 0px;
  width: 7.05rem;
  height: 14.1rem;
  overflow: hidden;
}

.tip-content {
  padding: 1.5625rem 0.625rem;
  font-size: 0.875rem;
  color: #fff8c5;
  line-height: 1.5;
}

.toast-btn div {
  background-image: -moz-linear-gradient(
    -18deg,
